import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios'

Vue.use(Vuex);
const state = {
  num:[1],
  aa: 2
}
const mutations = {
  add(state) {
    let that = this;
    axios({
        method: "post", //指定请求方式
        url: "/listAdd", //请求接口（相对接口，后面会介绍到）
        // headers: {'content-type': 'application/x-www-form-urlencoded'},
        // withCredentials: true
        data: {
          obj: {
            content: "事水高己想和。",
            name: "崔秀兰"
          }
        }
      })
      .then(function (res) {
          state.num=res
        // console.log(state.num)
        // that.state.num=res
        
        
        //接口成功返回结果执行
      })
      .catch(function (err) {
        //请求失败或者接口返回失败或者.then()中的代码发生错误时执行
      });





    //   let obj={
    //     content: "事水高己想和。",
    //     name: "崔秀兰"
    //   }
    //   axios('/listAdd','post',JSON.stringify(obj))
    //    .then(function (res) {
    //      console.log(res)
    //      state.num=res
    //    })
    //    .catch(function (err) {
    //      console.log(err)

    //    })
  }
}
export default new Vuex.Store({
  state,
  mutations
})



